@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
#root{display:grid;grid-template-rows:auto 60%;grid-template-columns:repeat(auto-fit, 1fr);grid-template-areas:"title" "level-selection";justify-items:stretch;align-items:center}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:linear-gradient(rgba(34,33,33,0.75), rgba(34,33,33,0.75)),url(/lowPolyRobotsAttack/static/media/retrowave-background.ac5c1261.jpg);background-size:cover;font-family:'Press Start 2P', cursive}.title{grid-area:title;height:100%;width:100%;text-align:center}.title div{margin-top:16vh;margin-left:8vw;margin-right:8vw;margin-bottom:4vh}.title h1{font-size:3rem}.glow{font-size:3rem;color:#FF83CF;text-align:center;-webkit-animation:glow 1s ease-in-out infinite alternate;animation:glow 1s ease-in-out infinite alternate}@-webkit-keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}@keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}.title h4{color:#FF83CF}.title h5{color:#D11087;font-size:1rem}.title h6{color:white}.lvl-selection{grid-area:level-selection;background-color:rgba(0,0,0,0.6);width:100%}.lvl-selection .card-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));align-items:center;grid-gap:2.5rem;padding:3.5rem}.card{padding:1rem;border:2px solid #00B9F0;height:95%;width:80%;color:white;text-align:center;transition-timing-function:ease-out;transition:0.2s}.card:hover{transition-timing-function:ease-in;transition:0.2s;transform:translateY(-5%);-webkit-animation:glow-card 1s ease-in-out infinite alternate;animation:glow-card 1s ease-in-out infinite alternate}@-webkit-keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}@keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}.card h4{color:#00B9F0}.card p{text-align:center;font-size:0.95rem}.card button{margin-top:3vh;margin-bottom:4vh;width:100%;height:20%;font-family:'Press Start 2P', cursive;background-color:#00B9F0;border:none}

